import uniIcon from "@/components/uni-icon/uni-icon.vue"
export default {
	components: {uniIcon},
	data(){
		return{
			contract:{},
			listIndex:0,
			id:'',
			type:'',
			usable:''
		}
	},
	methods:{
		onLoad(options){
			this.id=options.id;
			this.type=options.type;
			if(this.type==0){
				this.usable=true;
			}else{
				this.usable=false;
			}
			if(options.id==1){
				this.contract={
				id:'1',
				landlord:'黎明',
				house:'蒋村花园22栋1单元402-2',
				time:'2017-09-03至2018-09-02',
				imgList:[
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561459911707&di=6a406958902f50240a51ccfc9f4722dc&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201307%2F05%2F20130705123817_24MLX.jpeg",
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561458690944&di=4692fe07c866f18c81f7e53602f92ac2&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20180826%2Fdf2a785deaec4472ad29ca3b9b4a2e06.jpeg",
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561459911706&di=a67f1db77e60c6a6d5da57ca95f0836e&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201307%2F05%2F20130705123735_PL8A4.jpeg"
				],
				imgCount:'3'
			    }
			}else if(options.id==2){
				this.contract={
					id:'2',
					landlord:'黎明',
					house:'东方郡东区9栋1203',
					time:'2019-01-03至2020-01-02',
					imgList:[
						"http://img5.duitang.com/uploads/item/201307/05/20130705123753_PTMex.jpeg",
						"http://img5.imgtn.bdimg.com/it/u=534001138,1355763079&fm=26&gp=0.jpg",
						"http://cdn.duitang.com/uploads/item/201404/16/20140416083918_3st2i.jpeg"
					],
					imgCount:'3',
			    }
			}else{
				this.contract={
				id:'3',
				landlord:'黎明',
				house:'蒋村花园22栋1单元402-2',
				time:'2018-01-04至2019-01-03',
				imgList:[
					"http://img5q.duitang.com/uploads/item/201307/05/20130705123713_KVcWe.jpeg",
					"http://bbs-fd.zol-img.com.cn/t_s800x5000/g3/M09/0F/07/Cg-4WFCX7r-ID0DbAACdgn-Kx-EAAA9lgMmlnIAAJ2a236.jpg",
					"http://imgsrc.baidu.com/forum/w%3D580/sign=d05d5b47004f78f0800b9afb49300a83/db529e2f0708283834801907bb99a9014d08f1c6.jpg"
				],
				imgCount:'3',
				}
			}	
		},
		back(){
			if(this.listIndex==0){
				this.listIndex=this.contract.imgCount-1;
			}else{
				this.listIndex--;
			}
		},
		forward(){
			if(this.listIndex==this.contract.imgCount-1){
				this.listIndex=0;
			}else{
				this.listIndex++;
			}
		},
		terminate(key){
			uni.navigateTo({
			  url:`./request?key=${key}`,
			  success: res => {},
			  fail: () => {},
			  complete: () => {}
			});
		}
	}
}
